<script>
export default {
  name: "Home",
  data() {
    return {
      slide: 0,
      mainProps: {
        blank: true,
        blankColor: "#777",
        width: 75,
        height: 75,
        class: "m1"
      },
      routerTitle: "",
      newsData: [],
      loadingNews: false
    };
  },
  created() {
    this.loadingNews = true;
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.$api.fetchGet("/news/firstPage").then(response => {
        this.newsData = response.data.items;
        this.loadingNews = false;
      });
    },
    goDetails(id) {
      this.$router.push(`/news/${id}`);
    },
    goPage() {
      this.routerTitle = "router-title";
    }
  }
};
</script>

