// Promise(get)
export function fetchGet (url, param) {   
  return new Promise((resolve, reject) => {
    axios.get(url, {params: param})
      .then(response => {
        resolve(response)
      }, err => {
        reject(err)
      })
      .catch((error) => {
        reject(error)
      })
  })
}

// Promise(post)
export function fetchPost (url, data) {   
    return new Promise((resolve, reject) => {
      axios.post(url, data)
        .then(response => {
          resolve(response)
        }, err => {
          reject(err)
        })
        .catch((error) => {
          reject(error)
        })
    })
  }

  // Promise(delete)
export function fetchDelete (url, data) {   
    return new Promise((resolve, reject) => {
      axios.delete(url, data)
        .then(response => {
          resolve(response)
        }, err => {
          reject(err)
        })
        .catch((error) => {
          reject(error)
        })
    })
  }

 // Promise(delete)
export function fetchPut (url, data) {   
  return new Promise((resolve, reject) => {
    axios.put(url, data)
      .then(response => {
        resolve(response)
      }, err => {
        reject(err)
      })
      .catch((error) => {
        reject(error)
      })
  })
}

export default {
  fetchGet,
  fetchPost,
  fetchDelete,
  fetchPut,
  axios
}
